@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieRow {
  margin-bottom: 30px;
}

.movieRow h2 {
  margin: 0 0 0 30px;
}

.movieRow--listarea {
  overflow-x: hidden;
  padding-left: 30px;
}

.movieRow--list {
  transition: all ease 500ms;
}

.movieRow--item {
  display: inline-block;
  width: 150px;
}

.movieRow--item img {
  -webkit-transform: scale( 0.9 );
          transform: scale( 0.9 );
  transition: all ease 0.3s;
  width: 100%;
}

.movieRow--item img:hover {
  -webkit-transform: scale( 1 );
          transform: scale( 1 );
}

.movieRow--left , .movieRow--right {
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba( 0 , 0 , 0 , 0.6 );
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  font-size: large;
  height: 225px;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  transition: all ease 300ms;
  width: 40px;
  z-index: 99;
}

.movieRow--left {
  left: 0;
}

.movieRow--right {
  right: 0;
}

.movieRow:hover .movieRow--left , .movieRow:hover .movieRow--right {
  opacity: 1;
}

@media ( max-width : 760px ) {

  .movieRow--left , .movieRow--right {
    opacity: 1;
  }
}

.featured {
  height: 100vh;
}

.featured--vertical {
  background: linear-gradient( to top , #111 10% , transparent 90% );
  height: inherit;
  width: inherit;
}

.featured--horizontal {
  background: linear-gradient( to right , #111 30% , transparent 70% );
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: inherit;
  -webkit-justify-content: center;
          justify-content: center;
  padding-bottom: 150px;
  padding-left: 30px;
  padding-top: 70px;
  width: inherit;
}

.featured--name {
  font-size: 60px;
  font-weight: 900;
}

.featured--info {
  font-size: 18px;
  font-weight: 900;
  margin-top: 15px;
}

.featured--points , .featured--year , .featured--seasons {
  display: inline-block;
  margin-right: 15px;
}

.featured--points {
  color: #46d369;
}

.featured--description {
  color: #999;
  font-size: 20px;
  margin-top: 15px;
  max-width: 40%;
}

.featured--buttons {
  margin-top: 15px;
}

.featured--watchbutton , .featured--mylistbutton {
  border-radius: 5px;
  display: inline-block;
  font-size: 20px;
  font-weight: 900;
  margin-right: 10px;
  opacity: 1;
  padding: 12px 25px;
  text-decoration: none;
  transition: all ease 0.2s;
}

.featured--watchbutton {
  background-color: white;
  color: black;
}

.featured--mylistbutton {
  background-color: #333;
  color: white;
}

.featured--genres {
  color: #999;
  font-size: 18px;
  margin-top: 15px;
}

.featured--watchbutton:hover , .featured--mylistbutton:hover {
  opacity: 0.7;
}

@media ( max-width : 760px ) {

  .featured {
    height: 80vh;
  }

  .featured--name {
    font-size: 40px;
  }

  .featured--info {
    font-size: 16px;
  }

  .featured--description {
    font-size: 14px;
    margin-right: 30px;
    max-width: 100%;
  }

  .featured--mylistbutton , .featured--watchbutton {
    font-size: 16px;
  }

  .featured--genres {
    font-size: 14px;
  }
}

header {
  -webkit-align-items: center;
          align-items: center;
  background: transparent;
  display: -webkit-flex;
  display: flex;
  height: 70px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  left: 0;
  padding: 0 30px;
  position: fixed;
  right: 0;
  top: 0;
  transition: all ease 0.5s;
  z-index: 999;
}

.black {
  background-color: #141414;
}

.header--logo {
  height: 30px;
}

.header--logo img {
  height: 100%;
}

.header--user {
  height: 40px;
}

.header--user img {
  border-radius: 5px;
  height: 100%;
}

* {
  box-sizing: border-box;
}

body {
  background-color: rgb( 17 , 17 , 17 );
  color: white;
  font-family: Roboto, sans-serif;
  margin: 0;
}

.lists {
  margin-top: -150px;
}

footer {
  margin-bottom: 15px;
  text-align: center;
}

#loading {
  -webkit-align-items: center;
          align-items: center;
  background-color: black;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 99;
}

#loading img {
  height: 100%;
}

